<template>
    <div>            
        <template v-for="(item, index) in actions">                        
            <a :style="item.style" v-if="item.type === 'click'" v-bind:key="'click-' + index" href="javascript:void(0);" @click="onClick(index)">                
                {{item.icon}}
            </a>
            
            <label :style="item.style" v-if="item.type === 'notification'" v-bind:key="'notification-' + index" @click="onClick(index)">
                {{item.icon}}
            </label>
            <label>&emsp;</label>
        </template>
        
    </div>
</template>
<script>
export default {
    name: "Action",
    props: {
        actions : {
            type: Array,
            default: [],           
        }   
    },
    methods : {
        onClick(index) {
            
            var payload = {
                index : index,
                id: this.actions[index].id
            };            
            this.$emit('action', payload);
        }
    }
}
</script>

