<template>
  <CCard>    
      <CCardHeader>
        <slot name="header">
          <CIcon name="cil-grid"/> {{caption}}
        </slot>
      </CCardHeader>
      <CCardBody>
        <CDataTable
          :hover="hover"
          :striped="striped"
          :border="border"
          :small="small"
          :fixed="fixed"
          :items="items"
          :fields="fields"
          :items-per-page="50"
          :dark="dark"
          pagination
        >                  
          <template #Actions="{item}">
            
            <td>                            
                <action :actions="item.Actions" v-on:action="actionClicked"></action>                
            </td>
          </template>
        </CDataTable>
        
      </CCardBody>    
  </CCard>
</template>

<script>
import Action from './../kobus/ActionItem.vue'
export default {
  name: 'Table',  
  components: {
    Action
  },
  props: {    
    items: Array,
    fields: {
      type: Array,
      default () {
        return ['username', 'registered', 'role', 'status']
      }
    },
    caption: {
      type: String,
      default: 'Table'
    },
    hover: Boolean,
    striped: Boolean,
    border: Boolean,
    small: Boolean,
    fixed: Boolean,
    dark: Boolean
  },
  methods: {
    actionClicked(event){
      this.$emit('action', event);
    },
    getBadge (status) {
      return status === 'Active' ? 'success'
        : status === 'Inactive' ? 'secondary'
          : status === 'Pending' ? 'warning'
            : status === 'Banned' ? 'danger' : 'primary'
    }
  }
}
</script>