<template>
  <div>        
    <CRow>
      <CCol lg="12">
        <pulse-loader v-if="invoiceLoading" :loading="invoiceLoading"></pulse-loader>
        <CTableWrapper v-on:action="actionTriggered" v-if="!invoiceLoading" :fields="['CNPJ', 'Date', 'Total', 'Actions']" :items="invoices" >          
          <template #header>
            <CIcon name="cil-grid"/> Invoices to analyze                        
            <div class="card-header-actions">
              <a 
                href="javascript:void(0)" 
                v-on:click="load()"
              >
                <CBadge color="secondary">LOAD</CBadge>
              </a>
            </div>
          </template>
        </CTableWrapper>        
      </CCol>            
    </CRow>
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import CTableWrapper from './base/Table.vue'

export default {
  name: 'InvoiceCorrection',
  components: { CTableWrapper, PulseLoader },
  computed: {
    invoiceLoading() { return !this.$store.state.invoice.invoiceLoaded},
    invoices() { return this.$store.state.invoice.invoices }
  },
  mounted: function() {
    if(this.$store.state.invoice.invoices.length === 0){
      this.load();
    }
    
  },
  methods: {            
    actionTriggered(event) {            
      if(event.index === 1) {
        this.edit(event.id)
      }
    },
    edit(index){
      this.$store.dispatch('invoice/selectInvoice', index);    
      this.$store.commit('invoice/setIsInvoicePublishedError', false)
      this.$router.push('/invoiceedition/' + index)      
    },
    load(){
      this.$store.dispatch('invoice/fetchInvoicesToAnalyze');    
    },   
  
  }
}
</script>
<style>
.spinner {
  display: block;
  position: fixed;
  z-index: 1031; /* High z-index so it is on top of the page */
  top: 50%;
  right: 50%; /* or: left: 50%; */
  margin-top: -..px; /* half of the elements height */
  margin-right: -..px; /* half of the elements widht */
}
</style>